.citiesWrapper {
    margin: 0 auto;
    width: 1300px;
    display: flex;
    text-align: center;
    padding-top: 100px;
}

.description .cities {
    width: 100%;
    height: 650px;
    border: 1px solid black;
    background-color: burlywood;
    margin-right: 20px;
    position: relative;
    img {
        position: relative;
        height: 100%;
        width: 100%;
        object-fit: cover;
        background: rgba(31, 25, 112, 0.342);
        opacity: 0.65;
        transition-duration: 0.3s;
        cursor: pointer;
    }
    img:hover {
        opacity: 0.3;
        transition-duration: 0.3s;
        cursor: pointer;
    }
    p {
        position: absolute;
        top: 0;
        left: 0;
        font-size: 20pt;
        font-weight: 900;
        padding-left: 20px;
        padding-top: 10px;
        z-index: 1;
        color: white;
    }
}

.description {
    color: #304663fc;
    line-height: 1.7;
}

.city {
    display: none;
    position: absolute;
    color: white;
    font-size: 12pt;
    font-weight: 400;
    width: 600px;
    top: 570px;
    left: 0;
    text-align: left;
}

.cities:hover .city {
    display: block;
    z-index: 1;
}

.footer {
    margin: 0 auto;
    width: 300px;
    padding-top: 20px;
    font-size: 12pt;
}

@media screen and (max-width: 1010px) {
    .citiesWrapper {
        display: block;
        width: 100%;
        padding-top: 60px;
    }
    .footer {
        padding-top: 10px;
        font-size: 10pt;
        text-align: center;
        padding-bottom: 10px;
    }
}
