.trip {
    text-align: center;
    background-size: cover;
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
    min-height: 100vh;
    margin: 0 auto;
    padding-bottom: 20px;
    color: #304663fc;
    line-height: 1.7;
}

.summaryText {
    line-height: initial;
    p {
        color: black;
        font-size: 16pt;
        text-align: justify;
        padding: 65px 25px 0 25px;
    }
}

.tripHeader {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
}

.trip .Navigation_header_links a {
    color: black;
}

.day {
    display: flex;
    margin: 150px;
    margin: 5px 0 0 150px;
    margin-left: 25px;
    text-decoration: none;
}

.footer {
    display: block;
    width: 100%;
    font-size: 12pt;
    text-align: center;
    padding-top: 10px;
    margin-bottom: 0;
}

@media screen and (max-width: 1010px) {
    .summaryText p {
        padding: 45px 25px 0 25px;
        font-size: 12pt;
    }
    .day {
        display: block;
        width: 86%;
    }
    .footer {
        padding: 0px;
        padding-top: 10px;
        font-size: 10pt;
    }
    .allDays {
        width: 100%;
    }
}
